<template>
  <div
    id='speakers'
    style='max-width: 900px margin: auto'
    class='grey lighten-3'
  >

    <v-container fluid>
        <!-- List of Meals -->
        <v-layout row wrap>
          <v-flex xs12 v-for='staff in filteredStaff' :key='staff.id'>
            <v-card color='pink darken-4' class='white--text'>
              <v-card-title primary-title>
                <div class="headline">
                  {{ staff.fname }} {{ staff.lname }}
                </div>
                <v-spacer></v-spacer>                      
                <v-avatar
                size="100px"
                >
                  <img
                    :src="picUrl(staff.pic120x120)"
                    alt=""
                  >
                </v-avatar>
              </v-card-title>
              <v-card-text>
                {{ staff.bioweb }}
              </v-card-text>
            </v-card>
            <v-card-text>
            </v-card-text>
          </v-flex>
        </v-layout>
  </v-container>
  </div>

</template>

<script>
export default {
  data () {
    return {
      searchString: '',
      showSearchInput: true,
      showTabs: true,
      active: '',
      subType: '',
      field: 'title',
      tabs: ['Tuesday', 'Wednesday', 'Thursday']
    }
  },
  computed: {
    filteredStaff: function () {
      return this.$store.getters.staff
    }
  },
  methods: {
    picUrl: function (file) {
      return 'https://charisfellowship.us/images/conference/instructors/' + file
    }
  },
  created () {
    this.$store.dispatch('updateFromLocalStorage', 'staff')
    this.$store.dispatch('updateFromAPI', 'staff')
  }
}
</script>




