<template>
 <div id="app">
  <v-app id="inspire">
    <v-layout column>
      <v-flex xs12 sm6>
       
        <v-card>
          <v-container grid-list-md>
            <v-layout row wrap>
              <v-flex
                v-for="card in cards"
                v-bind="{ [`xs${card.flex}`]: true }"
                :key="card.title"
                @click='GoTo(card.route)' 
                style='cursor:pointer'
                v-if='showMenuItem(card.title)'
              >
                <v-card color="blue accent-4">
                  <v-card-media
                    :src="card.src"
                    height="200px"
                  >
                    <v-container fill-height fluid>
                      <v-layout fill-height>
                        <v-flex xs12 align-end flexbox>
                          <span class="headline white--text display-3" v-text="card.title"></span>
                        </v-flex>
                      </v-layout>
                    </v-container>
                  </v-card-media>
                </v-card>
              </v-flex>
            </v-layout>
          </v-container>
        </v-card>
      </v-flex>
    </v-layout>
  </v-app>
</div>
</template>

<script>
export default {
  data () {
    return {
      message: 'Welcome to Access2018.app',
      cards: [
        { title: 'Welcome', src: 'https://charisfellowship.us/images/access2018.app/welcome.jpg', flex: 6, route: 'welcome' },
        { title: 'Feed Back', src: 'https://charisfellowship.us/images/access2018.app/feedback.png', flex: 6, route: 'surveyDialog' },
        { title: 'Schedule', src: 'https://charisfellowship.us/images/access2018.app/schedule.jpg', flex: 6, route: 'schedule' },
        { title: 'Cohorts & Workshops', src: 'https://charisfellowship.us/images/access2018.app/cohorts.jpg', flex: 6, route: 'cohorts' },
        { title: 'Maps', src: 'https://charisfellowship.us/images/conference/Building_Map_1st_Floor-icon.png', flex: 6, route: 'maps' },
        { title: 'Meals', src: 'https://charisfellowship.us/images/access2018.app/meals.jpg', flex: 6, route: 'meals' },
        { title: 'Speakers', src: 'https://charisfellowship.us/images/access2018.app/speakers.jpg', flex: 6, route: 'speakers' },
        { title: 'Staff', src: 'https://charisfellowship.us/images/access2018.app/staff.jpg', flex: 6, route: 'staff' },
        { title: 'Partners', src: 'https://charisfellowship.us/images/access2018.app/partners.jpg', flex: 6, route: 'partners' },
        { title: 'Chat', src: 'https://charisfellowship.us/images/access2018.app/chat.jpg', flex: 6, route: 'chat' },
        { title: 'Resources', src: 'https://charisfellowship.us/images/access2018.app/profile.jpg', flex: 6, route: 'resources' }
      ]
    }
  },
  methods: {
    GoTo: function (route) {
      this.$router.push('/' + route)
    },
    showMenuItem: function (title) {
      if (title === 'Feed Back') {
        return this.isBeforeTimeToShowFeedBack
      } else if (title === 'Welcome') {
        return !this.isBeforeTimeToShowFeedBack
      } else {
        return true
      }
    }
  },
  computed: {
    userIsAuthenticated: function () {
      return this.$store.getters.user !== null
    },
    today: function () {
      return new Date()
    },
    showFeedBackAfter: function () {
      return new Date(2018, 6, 25)
    },
    isBeforeTimeToShowFeedBack: function () {
      return this.today > this.showFeedBackAfter
    }
  }
}
</script>



