<template>
    <div>
    <!-- Container for content with margin -->
    <v-container
      fluid
      style='min-height: 0'
      grid-list-lg
    >
      <!-- Search Input -->
      <v-layout row wrap v-show="showSearchInput">
        <v-flex xs8>
          <v-text-field
            id='search-chats'
            name='search-chats-input'
            label='Search'
            v-model='searchString'
            v-show='showSearchInput'
          >
          </v-text-field>
        </v-flex>          
      </v-layout>

      <!-- Show Filters Chatter  -->
      <v-layout row wrap v-show="searchString.length">
        <v-flex xs8>
          <h3 @click='toggleSearchString' style="cursor:pointer"> Showing chats by {{ searchString }} [clear]</h3>
        </v-flex>          
      </v-layout>

      <!-- Post new chat -->
      <v-layout row wrap>
        <v-flex xs12 v-show="!searchString.length">
            <form>
            <v-text-field
              v-model="chat"
              label="Message"
              multi-line
            ></v-text-field>
            <v-btn @click="submitChat">submit</v-btn>
          </form>
        </v-flex> 

        <!-- List of Chats -->
        <v-flex xs12 v-for='(chat, index) in filteredChatroom' :key='index'>
          <v-card color='grey darken-4' class='white--text'>
          <v-card-text>
              <div v-html='formatChat(chat.chat)'></div>
          </v-card-text>
          <v-card-text>
              <div v-if="!searchString.length" v-html='chat.name' class="text-xs-right" @click="toggleSearchString(chat.name)" style='cursor: pointer'></div>
              <div v-if="isPostMine(chat.userid)" v-html='"Delete"' class="text-xs-left" @click="deletePost(chat.key)" style='cursor: pointer'></div>
          </v-card-text>
          </v-card>
        </v-flex>
        <v-flex xs12>
          <v-card color='grey darken-4' class='white--text'>
          <v-card-text style='cursor: pointer'>
              <div>Showing {{ filteredChatRoomCount }} chats.</div>
              <v-btn
              color="blue"
              dark
              small
              absolute
              top
              right
              fab
              @click='increaseShowChatsCount(20)'
              v-if="showIncrementer"
            >
            <v-icon>add</v-icon>
          </v-btn>   
          </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>

    </v-container>
    </div>
</template>

<script>
import * as firebase from 'firebase'
// import store from '../store/store'

export default {
  data () {
    return {
      message: 'Welcome',
      meetups: [],
      chatroom: [],
      name: '',
      email: '',
      chat: '',
      valid: true,
      searchString: '',
      showSearchInput: false,
      limitToFirst: 50,
      showChatsCountIncrement: 10
    }
  },
  computed: {
    filteredChatroom: function () {
      let self = this
      let chatroomForCategory = this.categoryChatroom(self.chatroom)
      if (!self.searchString.length) { return chatroomForCategory.slice(0, this.showChatsCountIncrement) }
      return chatroomForCategory.filter(function (e) {
        if (e.name === self.searchString) {
          return true
        } else {
          return false
        }
      })
    },
    filteredChatRoomCount: function () {
      return this.filteredChatroom.length
    },
    chatRoomCount: function () {
      return this.chatroom.length
    },
    showIncrementer: function () {
      return this.chatRoomCount > this.filteredChatRoomCount
    },
    userId: function () {
      return this.$store.getters.userId
    },
    cat: function () {
      return this.$route.params.cat || 'general'
    }
  },
  methods: {
    submitChat: function () {
      let obj = {}
      obj.chat = this.chat
      obj.name = this.$store.getters.user.displayName
      obj.userid = this.$store.getters.user.id
      obj.datetime = Date.now()
      obj.cat = this.cat
      obj.reverseDatetime = -Date.now()
      firebase.database().ref('chatroom').push(obj).then(console.log('pushed')).catch((error) => console.log(error))
      this.chat = ''
      this.name = ''
    },
    snapshotToArray: function (snapshot) {
      var returnArr = []
      snapshot.forEach(function (childSnapshot) {
        var item = childSnapshot.val()
        item.key = childSnapshot.key
        returnArr.push(item)
      })
      return returnArr
    },
    toggleSearchString: function (searchString) {
      if (this.searchString.length) {
        this.searchString = ''
      } else {
        this.searchString = searchString
      }
    },
    increaseShowChatsCount: function (count) {
      this.showChatsCountIncrement = this.showChatsCountIncrement + count
    },
    formatChat: function (chat) {
      return chat.replace(/\n/g, '<br/>')
    },
    isPostMine: function (chatUserId, thisUserId = this.userId) {
      return chatUserId === thisUserId || this.$store.getters.user.id === 'wjwPJF5wBiYuA1W5JVVLhtv0JFz1'
    },
    deletePost: function (key) {
      firebase.database().ref('chatroom').child(key).remove().then(console.log('removed')).catch((error) => console.log(error))
    },
    categoryChatroom: function () {
      let self = this
      return this.chatroom.filter(function (e) {
        if (e.cat === self.cat) {
          return true
        } else {
          return false
        }
      })
    }
  },
  created () {
    firebase.database().ref('chatroom').orderByChild('reverseDatetime').limitToFirst(this.limitToFirst).on('value', (snapshot) => {
      this.chatroom = this.snapshotToArray(snapshot)
    })
  }
}
</script>



