<template>
  <div
    id='courses'
    style='max-width: 900px margin: auto'
    class='grey lighten-3'
  >

    <!-- Container for content with margin -->
    <v-container
      fluid
      style='min-height: 0'
      grid-list-lg
    >
      <v-layout row wrap v-if="showAddNew">
        <v-btn @click="$router.push('/resourcesDialog')">Add a new resource</v-btn>
      </v-layout>

      <v-layout row wrap v-if="showFilters">
        <v-flex xs6>
          <v-select
            :items='cohorts'
            item-text='title'
            item-value='title'
            v-model="searchString"
            label="Select a Cohort"
            single-line=""
          ></v-select>
        </v-flex>
      </v-layout>

      <!-- Search Input -->
      <v-layout row wrap>
        <v-flex xs6>
          <v-text-field
            id='search-resources'
            name='search-resources-input'
            label='Search'
            v-model='searchString'
            v-show=showSearchInput
          >
          </v-text-field>
        </v-flex>          
        <v-flex>
          <v-btn @click="clearSearchString" v-if="searchString.length" style='cursor:pointer'>Clear Search</v-btn>
          <v-btn @click="goToQuestionsNew" style='cursor:pointer'>Post a new question</v-btn>
        </v-flex>
      </v-layout>
      <!-- List of Meals -->
      <v-layout row wrap>
        <v-flex xs12 v-for='question in filteredQuestions' :key='question.id'>
          <v-card :color='cardColor' class='black--text'>
            <v-card-title primary-title>
              <div v-html="question.question"></div>
            </v-card-title>
            <v-card-text>
              <div v-html="question.conferencecoursetitle"></div>
            </v-card-text>
          </v-card>
        </v-flex>
         <v-flex xs12 v-if="!filteredQuestions.length">
          <v-card :color='cardColor' class='black--text'>
            <v-card-title primary-title>
              No (questions posted yet for {{ searchString }}
            </v-card-title>
          </v-card>
        </v-flex>
      </v-layout>
     </v-container>

  </div>
</template>

<script>
export default {
  data () {
    return {
      message: 'Course Questions',
      showAddNew: false,
      showFilters: false,
      searchString: '',
      showSearchInput: true,
      cardColor: 'white'
    }
  },
  methods: {
    getMailto: function (email) {
      return 'mailto:' + email
    },
    setSearchString: function (cohort) {
      this.searchString = cohort
    },
    clearSearchString: function () {
      this.searchString = ''
    },
    goToQuestionsNew: function () {
      alert('This will open a new window in your browser to post a question. After you post your question close that window and return to access2018.app')
      window.open('https://charisfellowship.us/conference/coursequestions/new/?ismobile')
    }
  },
  computed: {
    filteredQuestions: function () {
      let self = this
      if (!self.searchString.length) { return self.cohortquestions }
      return this.cohortquestions.filter(function (e) {
        let match = false
        if (typeof e.question === 'string' && e.question.toLowerCase().includes(self.searchString.toLowerCase())) { match = true }
        if (typeof e.conferencecoursetitle === 'string' && e.conferencecoursetitle.toLowerCase().includes(self.searchString.toLowerCase())) { match = true }
        return match
      })
    },
    cohortquestions: function () { return this.$store.getters.cohortquestions }
  },
  created () {
    this.$store.dispatch('updateFromLocalStorage', 'cohortquestions')
    this.$store.dispatch('updateFromAPI', 'cohortquestions')
    if (this.$route.params.searchString) {
      this.searchString = this.$route.params.searchString
    }
  }
}
</script>


