<template>
  <div
    id='badges'
    style='max-width: 900px margin: auto'
    class='grey lighten-3'
  >

    <v-container fluid>
      <!-- Search Input -->
      <v-layout row wrap>
          <v-flex>
            <v-card>
              <v-card-title>
                "Access-ibles" are folks registered for Access2018. Kind of like "Incredibles" except easier to get to!
              </v-card-title>
            </v-card>
          </v-flex>
        <v-flex xs8>
          <v-text-field
            id='search-meals'
            name='search--meals-input'
            :label='searchLabel'
            v-model='searchString'
            v-show='showSearchInput'
          >
          </v-text-field>
        </v-flex>          
      </v-layout>  

        <!-- List of Meals -->
        <v-layout row wrap>
          <v-flex xs12 v-for='badge in filteredBadges' :key='badge.id'>
            <v-card color='pink darken-4' class='white--text'>
              <v-card-title primary-title>
                <div class="headline">
                  {{ badge.fullnamelastfirst }}
                </div>
              </v-card-title>
            </v-card>
            <v-card-text>
            </v-card-text>
          </v-flex>
        </v-layout>
        {{filteredBadges.length}}
  </v-container>
  </div>

</template>

<script>
export default {
  data () {
    return {
      searchString: '',
      showSearchInput: true,
      searchLabel: 'Search',
      message: 'Badges'
    }
  },
  computed: {
    filteredBadges: function () {
      let params = {
        searchString: this.searchString,
        resource: 'badges',
        field: 'fullnamelastfirst'
      }
      return this.$store.getters.filteredResource(params)
    }
  },
  methods: {
  },
  created () {
    this.$store.dispatch('updateFromLocalStorage', 'badges')
    this.$store.dispatch('updateFromAPI', 'badges')
  }
}
</script>




