<template>
  <div
    id='courses'
    style='max-width: 900px margin: auto'
    class='grey lighten-3'
  >

    <!-- Container for content with margin -->
    <v-container
      fluid
      style='min-height: 0'
      grid-list-lg
    >
      <v-layout row wrap>
        <v-btn @click="onSubmit">Add a new resource</v-btn>
      </v-layout>

      <v-layout row wrap v-if="showFilters">
        <v-flex xs6>
          <v-select
            :items='cohorts'
            item-text='title'
            item-value='title'
            v-model="searchString"
            label="Select a Cohort"
            single-line=""
          ></v-select>
        </v-flex>
      </v-layout>

      <!-- Search Input -->
      <v-layout row wrap>
        <v-flex xs6>
          <v-text-field
            id='search-resources'
            name='search-resources-input'
            label='Search'
            v-model='searchString'
            v-show=showSearchInput
          >
          </v-text-field>
        </v-flex>          
        <v-flex>
          <v-chip @click="clearSearchString" v-if="searchString.length">Clear Search</v-chip>
        </v-flex>
      </v-layout>
      <!-- List of Meals -->
      <v-layout row wrap>
        <v-flex xs12 v-for='resource in filteredResources' :key='resource.id'>
          <v-card :color='cardColor' class='black--text'>
            <v-card-title primary-title>
              <div v-html="resource.idea"></div>
            </v-card-title>
            <v-card-text>
              <p v-if="resource.link.length" style='cursor:pointer' @click="visitUrl(resource.link)">Linked Resource: {{ resource.link }}</p>
              <p v-if="resource.fileUrl">Download: <a :href=resource.fileUrl target='_new'>{{ resource.fileName }}</a></p>
              <p>Posted: {{ formatDate(resource.reverseDatetime) }} by <a :href=getMailto(resource.authorEmail) v-html=resource.authorName />
              <span v-if="resource.cohort" @click="setSearchString(resource.cohort)" style="cursor:pointer">
                  about {{ resource.cohort }}
              </span>
              <span v-if="resource.authorPhone">
                  Mobile Phone: {{ resource.authorPhone }}
              </span>
              </p>
              <p v-if="isResourceMine">
                <span @click="onEdit(resource.key)" style='cursor:pointer'>Edit</span>
                <span @click="deleteResource(resource.key)" style='cursor:pointer'>Delete</span>
              </p>
            </v-card-text>
          </v-card>
        </v-flex>
         <v-flex xs12 v-if="!filteredResources.length">
          <v-card :color='cardColor' class='black--text'>
            <v-card-title primary-title>
              No resources posted yet for {{ searchString }}
            </v-card-title>
          </v-card>
        </v-flex>
      </v-layout>
      <span v-if="isMobile()">This is a mobile phone</span>
     </v-container>

  </div>
</template>

<script>
import * as firebase from 'firebase'
import moment from 'moment'

export default {
  data () {
    return {
      message: 'Resources Page',
      limitToFirst: 100,
      resources: [],
      showSearchInput: true,
      searchString: '',
      cardColor: 'white',
      showFilters: false,
      close1: true
    }
  },
  computed: {
    filteredResources: function () {
      let self = this
      if (!self.searchString.length) { return self.resources }
      return this.resources.filter(function (e) {
        let match = false
        if (typeof e.idea === 'string' && e.idea.toLowerCase().includes(self.searchString.toLowerCase())) { match = true }
        if (typeof e.cohort === 'string' && e.cohort.toLowerCase().includes(self.searchString.toLowerCase())) { match = true }
        if (typeof e.fileName === 'string' && e.fileName.toLowerCase().includes(self.searchString.toLowerCase())) { match = true }
        if (typeof e.authorName === 'string' && e.authorName.toLowerCase().includes(self.searchString.toLowerCase())) { match = true }
        return match
      })
    },
    cohorts: function () { return this.$store.getters.courses }
  },
  methods: {
    onSubmit: function () {
      let self = this
      if (self.searchString) {
        this.$router.push('/resourcesDialog/' + self.searchString)
      } else {
        this.$router.push('/resourcesDialog')
      }
    },
    snapshotToArray: function (snapshot) {
      var returnArr = []
      snapshot.forEach(function (childSnapshot) {
        var item = childSnapshot.val()
        item.key = childSnapshot.key
        returnArr.push(item)
      })
      return returnArr
    },
    getMailto: function (email) {
      return 'mailto:' + email
    },
    setSearchString: function (cohort) {
      this.searchString = cohort
    },
    clearSearchString: function () {
      this.searchString = ''
    },
    setShowFilters: function () {
      this.searchString = ''
      this.showFilters = true
    },
    setHideFilters: function () {
      this.searchString = ''
      this.showFilters = false
    },
    formatDate: function (value) {
      value = -value
      return moment(value).format('MMMM Do YYYY, h:mm:ss a')
    },
    fixUrl: function (url) {
      if (!url.includes('http')) { url = 'http://' + url }
      return url
    },
    visitUrl: function (url) {
      let newUrl = this.fixUrl(url)
      window.open(newUrl)
    },
    isMobile: function () {
      if (
        navigator.userAgent.match(/Android/i) ||
        navigator.userAgent.match(/webOS/i) ||
        navigator.userAgent.match(/iPhone/i) ||
        navigator.userAgent.match(/BlackBerry/i) ||
        navigator.userAgent.match(/Windows Phone/i)
      ) {
        return true
      } else {
        return false
      }
    },
    isResourceMine: function () {
      return this.$store.getters.user.id === 'wjwPJF5wBiYuA1W5JVVLhtv0JFz1'
    },
    onEdit: function (key) {
      this.$router.push('/resourceDialogEdit/' + key)
    },
    deleteResource: function (key) {
      if (confirm('Are you sure?')) {
        firebase.database().ref('resources').child(key).remove().then(console.log('removed')).catch((error) => console.log(error))
      }
    }
  },
  created () {
    firebase.database().ref('resources').orderByChild('reverseDatetime').limitToFirst(this.limitToFirst).on('value', (snapshot) => {
      this.resources = this.snapshotToArray(snapshot)
      this.$store.dispatch('updateFromLocalStorage', 'courses')
      this.$store.dispatch('updateFromAPI', 'courses')
      if (this.$route.params.searchString) {
        this.searchString = this.$route.params.searchString
      }
    })
  }
}
</script>




