<template>
  <div
    id='meals'
    style='max-width: 900px margin: auto'
    class='grey lighten-3'
  >
    <!-- Tabs show at the top wall to wall -->
    <v-tabs
      v-model='active'
      color='cyan'
      dark
      slider-color='yellow'
      v-show='showTabs === true'
    >
    <v-tab @click='setSearchString("")'>
      Meals: 
    </v-tab>
      <v-tab
        v-for='tab in tabs'
        :key='tab'
        ripple
        @click='setSearchString(tab)'
      >
        {{ tab }}
      </v-tab>
    </v-tabs>

    <!-- Container for content with margin -->
    <v-container fluid>

      <!-- Search Input -->
      <v-layout row wrap>
        <v-flex xs8>
          <v-text-field
            id='search-meals'
            name='search--meals-input'
            :label='searchLabel'
            v-model='searchString'
            v-show='showSearchInput'
          >
          </v-text-field>
          <span v-if='showStatus' v-html='status' />
        </v-flex>          
      </v-layout>  
      <!-- List of Meals -->
      <v-layout row wrap>
        <v-flex xs12 v-for='meal in filteredMeals' :key='meal.id'>
          <v-card :color='cardColor' class='white--text' grid-list-md>
            <v-card-title primary-title>
              <div class="headline">
                {{ meal.buttondescription }} {{ dollarFormat(meal.cost) }}
              </div>
            </v-card-title>
            <v-card-text>
              {{ meal.description }}
            </v-card-text>
            <v-card-text>
              Student Center - Ticket Required
            </v-card-text>
          </v-card>
          <v-card-text>
          </v-card-text>
        </v-flex>
      </v-layout>

    </v-container>
  </div>
</template>

<script>
export default {
  data () {
    return {
      searchString: '',
      showSearchInput: true,
      showTabs: true,
      searchLabel: 'Search',
      active: '',
      subType: '',
      field: 'title',
      tabs: ['Tuesday', 'Wednesday', 'Thursday', 'Search']
    }
  },
  computed: {
    status: function () {
      return this.$store.getters.status
    },
    showStatus: function () {
      return this.$store.getters.showStatus
    },
    meals: function () {
      return this.$store.getters.meals
    },
    filteredMeals: function () {
      let params = {
        searchString: this.searchString,
        subType: this.subType,
        resource: 'meals',
        field: 'buttondescription'
      }
      return this.$store.getters.filteredResource(params)
    },
    cardColor: function () {
      return this.$store.getters.scheduleColors.meals
    }
  },
  methods: {
    dollarFormat: function (number) {
      if (typeof number === 'string') { number = Number(number) }
      let newNumberString = number.toFixed(2)
      return '$' + newNumberString
    },
    setSearchString: function (string) {
      if (!string.includes('Search')) {
        this.searchString = string
        this.showSearchInput = false
      } else {
        this.searchString = ''
        this.showSearchInput = true
      }
    }
  },
  created () {
    this.$store.dispatch('updateFromLocalStorage', 'meals')
    this.$store.dispatch('updateFromAPI', 'meals')
  },
  updated () {
    if (this.$route.params.searchString) {
      this.searchString = this.$route.params.searchString
      this.showSearchInput = false
      this.showTabs = false
    }
  }
}
</script>



