// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import * as firebase from 'firebase'
import router from './router'
import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.min.css'
import store from './store/store'
import wysiwyg from 'vue-wysiwyg'
import './../node_modules/vue-wysiwyg/dist/vueWysiwyg.css'

Vue.use(Vuetify)

Vue.use(wysiwyg, {
  hideModules: {
    'link': true,
    'code': true,
    'image': true,
    'table': true
  }
})

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>',
  created () {
    firebase.initializeApp({
      apiKey: 'AIzaSyBHZNC1iw6NRbxfpF5riFPQ9WvDLS7vqtM',
      authDomain: 'access2018-1512686372387.firebaseapp.com',
      databaseURL: 'https://access2018-1512686372387.firebaseio.com',
      projectId: 'access2018-1512686372387',
      storageBucket: 'gs://access2018-1512686372387.appspot.com',
      messagingSenderId: '823122603601'
    })
    const messagingOn = false
    if (messagingOn) {
      navigator.serviceWorker.register('/static/firebase-messaging-sw.js')
        .then((registration) => {
          messaging.useServiceWorker(registration)
        }).catch(err => {
          console.log(err)
        })
      const messaging = firebase.messaging()
      messaging.requestPermission()
        .then(function () {
          console.log('Have Permission')
          return messaging.getToken()
        })
        .then(function (token) {
          console.log(token)
        })
        .catch(function (err) {
          console.log(err)
        })
      messaging.onMessage(function (payload) {
        console.log('onMessage: ', payload)
      })
    }
    firebase.auth().onAuthStateChanged((user) => {
      if (user) {
        this.$store.dispatch('autoSignIn', user)
        this.$store.dispatch('fetchUserData', user)
      }
    })
    if (window.location.hostname !== 'localhost') {
      this.$router.replace('/home')
    }
  }
})



