import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import * as firebase from 'firebase'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    message: 'Vuex Getter Working',
    courses: [],
    cohortquestions: [],
    schedule: [],
    meals: [],
    speakers: [],
    staff: [],
    badges: [],
    partners: [],
    announcements: [],
    status: '',
    imageUrl: '',
    showStatus: false,
    loading: false,
    error: null,
    messagingOn: false,
    resources: {
      'courses': 'https://charisfellowship.us/api/courses',
      'meals': 'https://charisfellowship.us/api/meals',
      'schedule': 'https://charisfellowship.us/api/schedule',
      'speakers': 'https://charisfellowship.us/api/speakers',
      'staff': 'https://charisfellowship.us/api/personalities',
      'partners': 'https://charisfellowship.us/api/partners',
      'announcements': 'https://charisfellowship.us/api/announcements',
      'cohortquestions': 'https://charisfellowship.us/api/coursequestions',
      'badges': 'https://charisfellowship.us/api/badges',
      'newestannouncement': 'https://charisfellowship.us/api/newestannouncement',
      'announcementApiCount': 'https://charisfellowship.us/api/announcementcount'
    },
    resource: '',
    ideas: [],
    user: null,
    adminId: 'wjwPJF5wBiYuA1W5JVVLhtv0JFz1',
    mostRecentAnnouncementViewed: '',
    newestannouncement: '',
    announcementCount: '',
    announcementApiCount: '',
    scheduleColors: {
      'meals': 'red darken-4',
      'speakers': 'purple darken-4',
      'cohorts': 'blue darken-4',
      'staff': 'green darken-4',
      'partners': 'white'
    }
  },
  getters: {
    courses: state => state.courses,
    cohortquestions: state => state.cohortquestions,
    schedule: state => state.schedule,
    meals: state => state.meals,
    speakers: state => state.speakers,
    staff: state => state.staff,
    badges: state => state.badges,
    status: state => state.status,
    resources: state => state.resources,
    ideas: state => state.ideas,
    announcements: state => state.announcements,
    partners: state => state.partners,
    showStatus: state => state.showStatus,
    loading: state => state.loading,
    error: state => state.error,
    user: state => state.user,
    userProfile: state => state.userProfile,
    adminId: state => state.adminId,
    dayOfWeek: state => state.dayOfWeek,
    imageUrl: state => state.imageUrl,
    scheduleColors: state => state.scheduleColors,
    messagingOn: state => state.messagingOn,
    announcementCount: state => state.announcementCount,
    announcementApiCount: state => state.announcementApiCount,
    resource: state => state.resource,
    filteredResource: (state) => (params) => {
      let data = state[params.resource].filter(el => {
        if (!Array.isArray(params.field)) {
          if (el[params.field].toLowerCase().includes(params.searchString.toLowerCase())) {
            return true
          } else {
            return false
          }
        } else {
          console.log('is Not Array')
        }
      })
      return data
    },
    coursesBySubtype: (state) => (subTypeFilter) => {
      return state.courses.filter(function (el) {
        if (subTypeFilter.includes(el.subtype)) {
          return true
        }
        return false
      })
    },
    scheduleByDay: (state) => (day) => {
      return state.schedule.filter(function (el) {
        if (day.includes(el.dayon)) {
          return true
        }
        return false
      })
    }
  },
  mutations: {
    updateResource (state, payload) {
      state[payload.resource] = payload.data
    },
    updateIdeas (state, payload) {
      state.ideas = payload
    },
    changeStatus (state, payload) {
      state.status = payload
    },
    setUser (state, payload) {
      state.user = payload
    },
    setUserProfile (state, payload) {
      state.userProfile = payload
    },
    setResource (state, payload) {
      state.resource = payload
    },
    setLoading (state, payload) {
      state.loading = payload
    },
    setError (state, payload) {
      state.error = payload
    },
    clearError (state) {
      state.error = null
    },
    setUserImageUrl (state, payload) {
      state.user.imageUrl = payload
    },
    setImageUrl (state, payload) {
      state.imageUrl = payload
    },
    setAnnouncementCount (state, payload) {
      state.announcementCount = payload
    },
    setMostRecentAnnouncementPosted (state, payload) {
      state.mostRecentAnnouncementPosted = payload
    }
  },
  actions: {
    setUserProfile ({commit}, payload) {
      const uuid = this.getters.user.id
      if (uuid) {
        firebase.database().ref('/users/' + uuid).set({
          displayName: payload.displayName,
          phoneNumber: payload.phoneNumber,
          email: payload.email,
          id: uuid
        })
          .then(function () {
            console.log('after update profile: ' + uuid)
            commit('setLoading', false)
            const newProfile = {
              email: payload.email,
              displayName: payload.displayName,
              phoneNumber: payload.phoneNumber,
              id: uuid
            }
            console.log('Set This User: ' + JSON.stringify(newProfile))
            commit('setUser', newProfile)
          })
      }
    },
    signUserUp ({commit, dispatch}, payload) {
      commit('setLoading', true)
      commit('clearError')
      firebase.auth().createUserWithEmailAndPassword(payload.email, payload.password)
        .then(
          user => {
            console.log('before update profile: ' + user.user.displayName)
            firebase.database().ref('/users/' + user.user.uid).set({
              displayName: payload.displayName,
              phoneNumber: payload.phoneNumber,
              email: payload.email,
              id: user.user.uid
            })
              .then(function () {
                dispatch('setUserProfile', {
                  displayName: payload.displayName,
                  phoneNumber: payload.phoneNumber,
                  email: payload.email,
                  id: user.user.uid
                })
              })
          }
        ).catch(
          error => {
            commit('setLoading', false)
            commit('setError', error.message)
            console.log(error)
          }
        )
    },
    signUserIn ({commit}, payload) {
      commit('setLoading', true)
      commit('clearError')
      firebase.auth().signInWithEmailAndPassword(payload.email, payload.password)
        .then(
          user => {
            commit('setLoading', false)
            console.log('Signing up: ' + user)
            const newUser = {
              id: user.user.uid,
              email: user.user.email,
              displayName: user.user.displayName
            }
            commit('setUser', newUser)
          }
        ).catch(
          error => {
            commit('setLoading', false)
            commit('setError', error.message)
          }
        )
    },
    logout ({commit}) {
      firebase.auth().signOut()
      commit('setLoading', false)
      commit('setUser', null)
    },
    updateUserImageAndProfile ({commit, getters}, payload) {
      if (payload.image) {
        let imageName = payload.image.name
        let storageRef = firebase.storage().ref('/userImages/' + imageName)
        storageRef.put(payload.image)
          .then((snapshot) => {
            snapshot.ref.getDownloadURL()
              .then((url) => {
                payload.imageUrl = url
                commit(('setImageUrl'), url)
              })
              .then(() => {
                firebase.database().ref('/users/' + getters.user.id)
                  .update(payload)
                  .then(() => {
                    commit('setUser', payload)
                  })
                  .catch((error) => {
                    console.log(error)
                  })
              })
              .catch((error) => {
                alert('not uploaded')
                console.log(error)
              })
          })
      } else {
        firebase.database().ref('/users/' + getters.user.id)
          .update(payload)
          .then(() => {
            commit('setUser', payload)
          })
          .catch((error) => {
            console.log(error)
          })
      }
    },
    updateIdeas ({commit, getters}, payload) {
      let databaseRef = firebase.database().ref('/resources/')
      let thisDatabaseRef = ''
      if (payload.file) {
        payload.fileName = payload.file.name
        let storageRef = firebase.storage().ref('/resources/' + payload.fileName)
        commit('setLoading', true)
        storageRef.put(payload.file)
          .then((snapshot) => {
            snapshot.ref.getDownloadURL()
              .then((url) => {
                payload.fileUrl = url
              })
              .then(() => {
                if (payload.key) {
                  thisDatabaseRef = firebase.database().ref('/resources/' + payload.key)
                  thisDatabaseRef.set(payload).catch((error) => { console.log(error) })
                } else {
                  databaseRef.push(payload).catch((error) => { console.log(error) })
                }
              })
              .catch((error) => {
                alert('not uploaded')
                console.log(error)
              })
          })
        commit('setLoading', false)
      } else {
        if (payload.key) {
          thisDatabaseRef = firebase.database().ref('/resources/' + payload.key)
          thisDatabaseRef.set(payload).catch((error) => { console.log(error) })
        } else {
          databaseRef.push(payload).catch((error) => { console.log(error) })
        }
      }
    },
    autoSignIn ({commit}, payload) {
      console.log('autosignin: ')
      commit('setUser', {
        id: payload.uid,
        displayName: payload.displayName,
        email: payload.email
      })
    },
    updateUserProfile ({commit, getters}, payload) {
      let newProfile = payload
      firebase.database().ref('/users/' + getters.user.id)
        .update(newProfile)
        .then(() => {
          commit('setUser', newProfile)
        })
        .catch((error) => {
          console.log(error)
        })
    },
    fetchUserData ({commit, getters}) {
      commit('setLoading', true)
      firebase.database().ref('/users/' + getters.user.id).once('value')
        .then(data => {
          const values = data.val()
          const updatedUser = {
            id: getters.user.id,
            email: values.email,
            displayName: values.displayName,
            phoneNumber: values.phoneNumber,
            imageUrl: values.imageUrl
          }
          commit('setLoading', false)
          commit('setUser', updatedUser)
        })
    },
    updateFromLocalStorage (context, resource) {
      var data = JSON.parse(localStorage.getItem(resource))
      context.commit('updateResource', {
        'data': data,
        'resource': resource
      })
    },
    updateFromAPI (context, resource) {
      let apiAddress = this.getters.resources[resource]
      axios
        .get(apiAddress)
        .then(response => {
          context.commit('updateResource', {
            'data': response.data,
            'resource': resource
          })
          console.log('Resource updated from online: ' + resource)
          localStorage.setItem(resource, JSON.stringify(response.data))
          console.log('Local storage updated with online resource: ' + resource)
          context.commit('changeStatus', resource.toUpperCase() + ' updated from online!')
        })
        .catch(() => {
          context.commit('changeStatus', resource.toUpperCase() + ' unable to update from online!')
          console.log('Something did not work')
        })
    }
  }
})



