<template>
  <div
    id='announcements'
    style='max-width: 900px margin: auto'
    class='grey lighten-3'
  >

  <!-- Container for content with margin -->
    <v-container fluid>

      <!-- Search Input -->
      <v-layout row wrap>
        <v-flex xs8>
          <v-tooltip top>
            <v-btn
              slot="activator"
              color="primary"
              dark
              fab
              @click="updateAnnouncements()"
            >
            <v-icon>update</v-icon>        
            </v-btn>
            <span>Check for new announcements</span>
          </v-tooltip>
          <span v-if="isAdmin" style='cursor:pointer' @click="goToAnnouncementDialog">New Announcement</span>
          <v-text-field
            id='search-announcements'
            name='search--announcements-input'
            :label='searchLabel'
            v-model='searchString'
            v-show='showSearchInput'
          >
          </v-text-field>
        </v-flex>          
      </v-layout>  

      <!-- List of Meals -->
      <v-layout row wrap>
        <v-flex xs12 v-for='announcement in filteredAnnouncments' :key='announcement.id'>
          <v-card color='orange darken-4' class='white--text'>
            <v-card-title primary-title>
              <div class="headline">
                {{ announcement.subject }}
              </div>
            </v-card-title>
            <v-card-text v-html='announcement.content'>
            </v-card-text>
            <v-card-text @click="visitUrl(announcement.link)" style='cursor:pointer' v-if="announcement.link.length" v-html="announcement.link">
            </v-card-text>
            <v-card-text v-html="announcement.dateposted">
            </v-card-text>
          </v-card>
          <v-card-text>
          </v-card-text>
        </v-flex>
      </v-layout>

    </v-container>

  </div>
</template>

<script>
import moment from 'moment'

export default {
  data () {
    return {
      searchString: '',
      showSearchInput: true,
      showTabs: true,
      searchLabel: 'Search',
      active: '',
      showStatus: true,
      status: '',
      mostRecentAnnouncementViewed: ''
    }
  },
  computed: {
    filteredAnnouncments: function () {
      let params = {
        searchString: this.searchString,
        resource: 'announcements',
        field: 'searchable'
      }
      return this.$store.getters.filteredResource(params)
    },
    announcementsCount: function () {
      return this.$store.getters.announcements.length
    },
    isAdmin: function () {
      if (this.$store.getters.user) {
        return this.$store.getters.user.id === this.$store.getters.adminId
      } else {
        return false
      }
    }
  },
  methods: {
    updateAnnouncements () {
      let self = this
      this.$store.dispatch('updateFromAPI', 'announcements')
        .then(function () {
          self.showStatus = true
          self.status = 'Updated'
          self.$store.commit('setAnnouncementCount', self.announcementsCount)
        })
        .then(
          setTimeout(this.toggleShowStatus(), 5000)
        )
    },
    visitUrl: function (url) {
      let newUrl = this.fixUrl(url)
      window.open(newUrl)
    },
    fixUrl: function (url) {
      if (!url.includes('http')) { url = 'http://' + url }
      return url
    },
    toggleShowStatus () {
      this.showStatus = false
    },
    formatDate: function (value) {
      let oldDate = value.toString()
      oldDate = oldDate.replace('{ts ', '').replace('}', '')
      return moment(oldDate).format('MMMM Do YYYY, h:mm:ss a')
    },
    goToAnnouncementDialog: function () {
      this.$router.push('/announcementDialog')
    }
  },
  created () {
    this.$store.dispatch('updateFromLocalStorage', 'announcements')
    let self = this
    this.$store.dispatch('updateFromAPI', 'announcements')
      .then(function () {
        self.$store.commit('setAnnouncementCount', self.announcementsCount)
      })
  }
}
</script>





