<template>
<div id="app">
  <v-app id="inspire">
    <v-layout row v-if="showKeithsWelcome">
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-card-media
            src="https://charisfellowship.us/images/access2018.app/minier.jpg"
            height="200px"
            contain
          >
          </v-card-media>
          <v-card-title primary-title>
            <div>
              <div class="headline">From Keith Minier - Access2018 Director</div>
              <span class="grey--text">Welcome to Fremont, Ohio and Grace Community.  I am very thankful you chose to spend part of your summer with extended spiritual family.  We as Charis Fellowship have much to rejoice over.  God continues to move in our tribe through our churches, our organization and our unique ministries.
</span>
            </div>
          </v-card-title>
          <v-card-actions v-show=!showMinierMore>
            <v-btn icon @click.native="toggleMinier">
              <v-icon>keyboard_arrow_down</v-icon>
            </v-btn>
          </v-card-actions>
          <v-slide-y-transition>
            <v-card-text v-show=showMinierMore>
                Ultimately, God continues to use the people of Charis.  Access is all about taking time to connect with the people in Charis so that we can leverage each others gifts, experiences and knowledge for the good of the Great Commission and Gods glory.  I trust the Main Celebrations, meals, cohorts and time just hanging out will be a fruitful part of the conference for you.  Thanks for being here.  Lets be praying God uses these few days to draw us closer to Him and His mission.<br/>-Keith
            </v-card-text>
          </v-slide-y-transition>
        </v-card>
      </v-flex>
    </v-layout>
    <v-divider dark v-if="showKeithsWelcome"></v-divider>
    <v-layout row v-if="showKevinsWelcome">
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-card-media
            src="https://charisfellowship.us/images/access2018.app/pinkerton.jpg"
            height="200px"
            contain
          >
          </v-card-media>
          <v-card-title primary-title>
            <div>
              <div class="headline">From Kevin Pinkerton - Access2018 Host Pastor</div>
              <span class="grey--text">At Grace Community Church, we are all pumped to host Access 2018 again!  Its always good to gather as a family of churches.  We have great volunteers who want to make your Access 2018 experience even better than last year.  </span>
            </div>
          </v-card-title>
          <v-card-actions v-show=!showPinkertonMore>
            <v-btn icon @click.native="togglePinkerton">
              <v-icon>keyboard_arrow_down</v-icon>
            </v-btn>
          </v-card-actions>
          <v-slide-y-transition>
            <v-card-text v-show=showPinkertonMore>
              <p>The Leaders of our Fellowship have put together another dynamic schedule of events that that will allow you to learn, celebrate and connect with other leaders who have common interests in your areas of ministry.  We hope your entire ministry team will enjoy every minute of this incredible, cost effective, opportunity.</p>
              <p>Fremont, in northwest Ohio, is the home of the Rutherford B. Hayes Presidential Center.  We also have other area attractions including Cedar Point, Kalahari, Lake Erie, and Marblehead Lighthouse.  The Fremont Community Recreation Complex next door to our church and the North Coast Inland Trail that runs along the south side of our main campus will be available for you to enjoy.</p>
              <p>Fremont is a small town, but we see God doing big things here.  We are excited to announce that we have just signed a purchase agreement to buy land and a light industrial building in Tiffin, Ohio.  Next year we plan to launch another campus of Grace, making it our third campus in four years.  Please pray with us about Tiffin.</p>
              <p>I look forward to celebrating with you at Grace Community!</p>
              <p>Kevin Pinkerton</p>
            </v-card-text>
          </v-slide-y-transition>
        </v-card>
      </v-flex>
    </v-layout>
    <v-divider dark v-if="showKevinsWelcome"></v-divider>
    <v-layout row v-if="showTomsWelcome">
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-card-media
            src="https://charisfellowship.us/images/access2018.app/avey.jpg"
            height="200px"
            contain
          >
          </v-card-media>
          <v-card-title primary-title>
            <div>
              <div class="headline">From Tom Avey - Access2018 Coordinator</div>
              <span class="grey--text">Welcome to Access2018. Last year at Access2017 we introduced this conference format and the reports have been encouraging. One of our pastors once told me "The answer to your ministry problems are found in the relationships you have with other leaders." </span>
            </div>
          </v-card-title>
          <v-card-actions v-show=!showAveyMore>
            <v-btn icon @click.native="toggleAvey">
              <v-icon>keyboard_arrow_down</v-icon>
            </v-btn>
          </v-card-actions>
          <v-slide-y-transition>
            <v-card-text v-show=showAveyMore>
              <p>We are pleased to enjoy together the hospitality of this great church. When you see Grace Community folks in the halways, be sure to thank the!</p>
              <p>We have also been blessed with the tireless work of Sandy Barrett as Operations Manager for the Charis Fellowship and for our annual conference. Sandy will be retiring from this roll after Access2018. Be sure to thank her for her love and sacrifice for all of us.</p>
              <p>And welcome Sharmion Bowell, who will take her place!</p>
              <p>Have an amazing time together at Access2018!</p>
              <p>Tom</p>
            </v-card-text>
          </v-slide-y-transition>
        </v-card>
      </v-flex>
    </v-layout>
  </v-app>
</div></template>

<script>
export default {
  data () {
    return {
      message: 'Need Welcome from Keith and Kevin',
      showAveyMore: false,
      showMinierMore: false,
      showPinkertonMore: false,
      showTomsWelcome: true,
      showKeithsWelcome: true,
      showKevinsWelcome: true
    }
  },
  methods: {
    closeAll: function () {
      this.showAveyMore = false
      this.showMinierMore = false
      this.showPinkertonMore = false
    },
    toggleAvey: function () {
      this.closeAll()
      this.showAveyMore = !this.showAveyMore
    },
    toggleMinier: function () {
      this.closeAll()
      this.showMinierMore = !this.showMinierMore
    },
    togglePinkerton: function () {
      this.closeAll()
      this.showPinkertonMore = !this.showPinkertonMore
    }
  }
}
</script>




