<template>
  <v-container>
  <v-form v-model="valid">
    <!-- <v-text-field
      v-model="name"
      :rules="nameRules"
      :counter="10"
      label="Name"
      required
    ></v-text-field>
    <v-text-field
      v-model="email"
      :rules="emailRules"
      label="E-mail"
      required
    ></v-text-field> -->
    <v-select
      :items='cohorts'
      item-text='title'
      item-value='title'
      v-model="cohort"
      label="Select a Cohort"
      single-line=""
    ></v-select>
  <p>Ideas, suggestions, best practices...<br/>
    (simple text, no links or images, add links or upload longer documents below)</p>
  <wysiwyg v-model="idea" />
    <v-text-field
      v-model="link"
      label="Link to a resource: provide a valid URL"
    ></v-text-field>
    <v-layout row v-if="!filename.length">
      <v-flex xs12>
        <p v-if="filename.length">File Selected: {{ filename }}</p>
        <v-btn raised class="primary" @click="onPickFile" v-html='imageButtonText' block></v-btn>
        <input 
          type="file" 
          style="display:none" 
          ref="fileInput"
          accept=".pdf,.doc,.docx,.xml,application/msword,application/vnd.openxmlformats-officedocument.wordprocessingml.document"
          @change="onFilePicked">
      </v-flex>
    </v-layout>
    <v-layout row v-if="filename.length">
      <v-flex xs12>
        <p v-if="filename.length">File Selected: {{ filename }}</p>
      </v-flex>
    </v-layout>
  <v-btn
      :disabled="!valid"
      @click="submit"
    >
      submit
    </v-btn>
  </v-form>
  {{ key }}
  </v-container>
</template>

<script>
  import * as firebase from 'firebase'

  export default {
    data: () => ({
      cohort: '',
      name: '',
      link: '',
      filename: '',
      key: '',
      idea: '',
      email: '',
      imageButtonText: 'Upload a resource',
      valid: false,
      nameRules: [
        v => !!v || 'Name is required',
        v => v.length >= 5 || 'Name must be greater than 5 characters'
      ],
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /^\w+([.-]?\w+)*@\w+([.-]?\w+)*(\.\w{2,3})+$/.test(v) || 'E-mail must be valid'
      ]
    }),
    computed: {
      cohorts: function () { return this.$store.getters.courses }
    },
    methods: {
      onPickFile () {
        this.$refs.fileInput.click()
      },
      onFilePicked (event) {
        const files = event.target.files
        this.filename = files[0].name
        if (this.filename.lastIndexOf('.') <= 0) {
          alert('Select a valid file')
        }
        const fileReader = new FileReader()
        fileReader.addEventListener('load', () => {
          this.fileUrl = fileReader.result
        })
        fileReader.readAsDataURL(files[0])
        this.file = files[0]
      },
      submit: function () {
        const newResource = {
          authorName: this.$store.getters.user.displayName,
          authorEmail: this.$store.getters.user.email,
          authorId: this.$store.getters.user.id,
          authorPhone: this.$store.getters.user.phoneNumber,
          link: this.link,
          idea: this.idea,
          reverseDatetime: -Date.now(),
          cohort: this.cohort
        }
        if (this.key) { newResource.key = this.key }
        if (this.file) { newResource.file = this.file }
        this.$store.dispatch('updateIdeas', newResource)
        this.$router.push('/resources')
      }
    },
    created () {
      this.$store.dispatch('updateFromLocalStorage', 'courses')
      this.$store.dispatch('updateFromAPI', 'courses')
      if (this.$route.params.cohort) {
        this.cohort = this.$route.params.cohort
      }
      if (this.$route.params.key) {
        this.key = this.$route.params.key
        console.log(this.key)
        firebase.database().ref('/resources/' + this.key).once('value')
          .then(data => {
            const values = data.val()
            console.log(values)
            this.cohort = values.cohort
            this.name = values.authorName
            this.link = values.link
            if (this.filename) { this.filename = values.filename }
            this.idea = values.idea
            this.email = values.authorEmail
          })
      }
    }
  }
</script>


