<template>
  <div
    id='courses'
    style='max-width: 900px margin: auto'
    class='grey lighten-3'
  >

    <!-- Tabs show at the top wall to wall -->
    <v-tabs
      v-model='active'
      color='cyan'
      dark
      slider-color='yellow'
      v-show='showTabs === true'
    >
    <v-tab @click='setTabFilter("Search All")'>
      COHORTS and WORKSHOPS:
    </v-tab>
      <v-tab
        v-for='tab in tabs'
        :key='tab'
        ripple
        @click='setTabFilter(tab)'
      >
        {{ tab }}
      </v-tab>
    </v-tabs>

    <!-- Container for content with margin -->
    <v-container
      fluid
      style='min-height: 0'
      grid-list-lg
    >

      <!-- Search Input -->
      <v-layout row wrap>
        <v-flex xs8>
          <v-text-field
            id='search-courses'
            name='search-courses-input'
            label='Search'
            v-model='searchString'
            v-show='showSearchInput === true'
          >
          </v-text-field>
          <span v-if='showStatus' v-html='status' />
        </v-flex>          
      </v-layout>

      <!-- List of Meals -->
      <v-layout row wrap>
        <v-flex xs12 v-for='course in filteredCourses' :key='course.id'>
          <v-card :color='cardColor' class='white--text'>
            <v-card-title primary-title>
              <div class='headline' @click='showCohort(course.title)' style='cursor: pointer'>{{ course.title }}</div>
              <v-btn
                color="blue"
                dark
                small
                absolute
                middle
                right
                fab
                @click='showCourse(course.title)'
                v-if='showDescription === false'
              >
              <v-icon>arrow_forward</v-icon>
            </v-btn>              
            <v-btn
                color="blue"
                dark
                small
                absolute
                middle
                right
                fab
                @click='setTabFilter("Search All")'
                v-if='showDescription'
              >
              <v-icon>arrow_back</v-icon>
            </v-btn>              
          </v-card-title>
          <v-card-text>
            <p>{{ dayon (course.subtype) }}</p>
            <div v-html='course.eventroom'></div>
          </v-card-text>
          <v-card-text>
               <div  v-if='showDescription' v-html='course.descriptionlong'></div>
          </v-card-text>
          <v-card-text>
              <v-btn @click='goToResources(course.title)' small>Resources</v-btn>
              <v-btn @click='goToQuestions(course.title)' small>Questions</v-btn>
              <v-btn @click='goToMap(course.map)' small v-if="course.map.length" v-html="mapButtonLabel(course.map)"></v-btn>
          </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>
    </v-container>

  </div>
</template>

<script>
export default {
  data () {
    return {
      searchString: '',
      subTypeFilter: '',
      subTypesDayOn: {
        'A': 'Tuesday from 11:00 to 12:15 and 2:30 - 3:30',
        'B': 'Wednesday from 11:00 to 12:15 and 2:30 - 3:30',
        'C': 'Tuesday and Wednesday from 11:00 to 12:15 and 2:30 - 3:30 (repeated)',
        'D': 'Tuesday through Wednesday (11:00 to 12:15 and 2:30 - 3:30)'
      },
      tabFilters: {
        'Tuesday': ['A', 'D'],
        'Wednesday': ['B', 'D']
      },
      tabs: ['Tuesday', 'Wednesday', 'Search All'],
      active: '',
      showDescription: false,
      showTabs: true,
      showSearchInput: true
    }
  },
  computed: {
    status: function () {
      return this.$store.getters.status
    },
    showStatus: function () {
      return this.$store.getters.showStatus
    },
    filteredCourses: function () {
      let self = this
      let params = {
        searchString: this.searchString,
        resource: 'courses',
        subType: this.subType,
        field: 'title'
      }
      if (self.subTypeFilter) {
        return this.$store.getters.coursesBySubtype(self.subTypeFilter)
      } else {
        return this.$store.getters.filteredResource(params)
      }
    },
    cardColor: function () {
      return this.$store.getters.scheduleColors.cohorts
    }
  },
  methods: {
    dayon: function (subtype) {
      return this.subTypesDayOn[subtype]
    },
    setTabFilter: function (subtype) {
      this.showSearchInput = false
      if (subtype === 'Search All') {
        this.showSearchInput = true
        this.subTypeFilter = ''
        this.showDescription = false
        this.searchString = ''
      } else {
        this.subTypeFilter = this.tabFilters[subtype]
      }
    },
    showCourse: function (title) {
      this.showDescription = true
      this.showSearchInput = false
      this.searchString = title
      this.subTypeFilter = ''
    },
    goToResources: function (title) {
      this.$router.push('/resources/' + title)
    },
    goToQuestions: function (title) {
      this.$router.push('/cohortquestions/' + title)
    },
    goToMap: function (map) {
      window.open(map)
    },
    mapButtonLabel: function (map) {
      return 'Map'
    }
  },
  created () {
    this.$store.dispatch('updateFromLocalStorage', 'courses')
    this.$store.dispatch('updateFromAPI', 'courses')
  }
}
</script>




