<template>
  <v-app>
    <!-- Nav drawer - comes up from left side -->
    <v-navigation-drawer v-model="sideNav" temporary absolute>
       <v-list class="pa-1">
        <v-list-tile @click="returnHome" style="cursor:pointer">
            <img src="https://access2018.com/images/logo6.png" >
        </v-list-tile>
      </v-list>
      <v-list class="pt-0" dense>
        <v-divider></v-divider>
        <v-list-tile v-for="item in navDrawerItems" :key="item.title" router :to="item.link">
          <v-list-tile-action>
            <v-icon>{{ item.icon }}</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
                {{ item.title }}
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
        <v-list-tile @click='onLogout' v-if=userIsAuthenticated>
          <v-list-tile-action>
            <v-icon>exit_to_app</v-icon>
          </v-list-tile-action>
          <v-list-tile-content>
            <v-list-tile-title>
                Logout {{ user.displayName }}
            </v-list-tile-title>
          </v-list-tile-content>
        </v-list-tile>
      </v-list>
    </v-navigation-drawer>

    <!-- Tool Bar at the top -->
    <v-toolbar>
      <v-toolbar-side-icon @click="sideNav = !sideNav"></v-toolbar-side-icon>
      <v-toolbar-title><router-link to='/'><img src="https://access2018.com/images/logo6.png"></router-link></v-toolbar-title>
      <v-spacer></v-spacer>
      <v-toolbar-items class="hidden-xs-only">
        <v-btn flat v-for="item in toolbarItems" :key="item.title" router :to="{ path: item.routeName, query: { cat: item.routeParam }}">
          <v-icon left>{{ item.icon }}</v-icon>
            {{ item.title }}
        </v-btn>
        <v-btn flat @click="onLogout" v-if=userIsAuthenticated>
          <v-icon left>exit_to_app</v-icon>
            Logout
        </v-btn>
      </v-toolbar-items>
    </v-toolbar>  

    <!-- Dynamic Page Contents -->
    <transition name="component-fade" mode="out-in">
    <router-view style="margin-bottom:50px"></router-view>
    </transition>


    <!-- Footer Nav Bar -->
    <v-bottom-nav :value='true' fixed color="white" height="50px">
        <v-btn
        flat
        router
        :to="'chat'"
      >
        Chat
      </v-btn>
      <v-tooltip top>
      <v-btn
        slot="activator" 
        flat
        v-show='showBack'
        @click="returnBack"
      >
        <v-icon large dark>arrow_back</v-icon>
      </v-btn>
      <span>Navigate Back</span>
      </v-tooltip>
      <v-tooltip top>
      <v-btn
        slot="activator" 
        flat
        v-show='showBack'
        @click="returnHome"
      >
        <v-icon large dark>home</v-icon>
      </v-btn>
      <span>Home</span>
      </v-tooltip>
      <v-btn
        flat
        router
        :to="'announcements'"
      >
        Announcements <span v-if="announcementCountNotViewed"> {{announcementCountNotViewed}} new </span>
      </v-btn>
     </v-bottom-nav>
  </v-app>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      sideNav: false,
      alwaysToolBarItems: [
        { title: 'Schedule', link: '/schedule/', routeName: 'Schedule', icon: 'schedule' }
      ],
      loggedInAdditionalToolBarItems: [
      ],
      loggedOutAdditionalToolBarItems: [
        { title: 'Sign Up', link: '/signup', routeName: 'Signup', icon: 'account_box' },
        { title: 'Sign In', link: '/signin', routeName: 'Signin', icon: 'account_box' }
      ],
      additionalNavDrawerItems: [
        { title: 'Speakers', link: '/speakers', routeName: 'Speakers', icon: 'people' },
        { title: 'Staff', link: '/staff', routeName: 'Staff', icon: 'contacts' },
        { title: 'Cohorts & Workshops', link: '/cohorts', routeName: 'Cohorts', icon: 'group' },
        { title: 'Resources', link: '/resources', routeName: 'Resources', icon: 'file_copy' },
        { title: 'Meals', link: '/meals', routeName: 'Meals', icon: 'room_service' },
        { title: 'Chat', link: '/chat', routeName: 'Chat', icon: 'chat' },
        { title: 'Maps', link: '/maps', icon: 'map' },
        { title: 'Grace Kids', link: '/childcare', icon: 'map' },
        { title: 'Grace Kids Schedule', link: '/childcareschedule', icon: 'map' },
        { title: 'Partners', link: '/partners', icon: 'group_work' },
        { title: 'Access-ibles', link: '/badges', icon: 'group_work' }
      ],
      loggedOutAdditionalNavDrawerItems: [
      ],
      loggedInAdditionalNavDrawerItems: [
        { title: 'Profile', link: '/profile', routeName: 'Profile', icon: 'account_box' }
      ],
      footerLinks: [
        { title: 'Chat', link: 'chat' },
        { title: 'Announcements', link: '/Announcements' }
      ],
      e1: 'False',
      showBack: true
    }
  },
  computed: {
    navDrawerItems: function () {
      let items = this.toolbarItems.concat(this.additionalNavDrawerItems)
      if (!this.userIsAuthenticated) {
        items = items.concat(this.loggedOutAdditionalNavDrawerItems)
      } else {
        items = items.concat(this.loggedInAdditionalNavDrawerItems)
      }
      return items
    },
    toolbarItems: function () {
      let items = this.alwaysToolBarItems
      if (!this.userIsAuthenticated) {
        items = items.concat(this.loggedOutAdditionalToolBarItems)
      } else {
        items = items.concat(this.loggedInAdditionalToolBarItems)
      }
      return items
    },
    resources: function () {
      return this.$store.getters.resources
    },
    userIsAuthenticated: function () {
      return this.$store.getters.user !== null
    },
    user: function () {
      return this.$store.getters.user
    },
    announcementCountNotViewed: function () {
      let datastored = this.$store.getters.announcementCount
      let dataapi = this.$store.getters.announcementApiCount
      if (datastored > dataapi) { return 0 }
      return dataapi - datastored
    }
  },
  methods: {
    getDataLocally: function (name) {
      var data = JSON.parse(localStorage.getItem(name))
      return data
    },
    loadDataIfNeeded: function (resourceName) {
      if (!this.getDataLocally(resourceName)) {
        let apiAddress = this.resources[resourceName]
        console.log(apiAddress)
        axios.get(apiAddress)
          .then(response => {
            localStorage.setItem(resourceName, JSON.stringify(response.data))
            console.log('Resources Loaded: ' + resourceName)
          })
          .catch((error) => console.log(error))
      }
    },
    onLogout () {
      this.$store.dispatch('logout')
      this.$router.push('/signin')
    },
    returnBack () {
      this.$router.go(-1)
    },
    returnHome () {
      this.$router.push('/')
    }
  },
  created () {
    for (let resource in this.resources) {
      this.loadDataIfNeeded(resource)
    }
    this.$store.dispatch('updateFromAPI', 'announcementApiCount')
    this.$store.commit('setAnnouncementCount', JSON.parse(localStorage.getItem('announcements')).length)
  },
  name: 'App'
}
</script>



