<template>
  <v-container>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <v-alert :value="true" type="error" v-model="alert" dismissible v-if="error">
          {{ error }}
        </v-alert>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <v-card>
          <v-card-media :src=imageUrl height="200px" contain v-if="showImageInput">
          </v-card-media>          
          <v-card-text>
            <v-container>
              <form @submit.prevent="onSubmit">
                <v-layout row>
                  <v-flex xs12>
                    <v-text-field
                      name="displayName"
                      label="Display Name"
                      id="displayName"
                      v-model="displayName"
                      type="text">
                    </v-text-field>    
                  </v-flex>
                </v-layout>
                <v-layout row>
                  <v-flex xs12>
                    <v-text-field
                      name="phoneNumber"
                      label="Phone Number"
                      id="phoneNumber"
                      v-model="phoneNumber"
                      type="text">
                    </v-text-field>    
                  </v-flex>
                </v-layout>
                <v-layout row v-if="showImageInput">
                  <v-flex xs12>
                    <v-btn raised class="primary" @click="onPickFile" v-html='imageButtonText'></v-btn>
                    <input 
                      type="file" 
                      style="display:none" 
                      ref="fileInput"
                      accept="image/*"
                      @change="onFilePicked">
                  </v-flex>
                </v-layout>
                <v-layout>
                  <v-flex xs12>
                    <v-btn type="submit">Submit Changes</v-btn>
                  </v-flex>
                </v-layout>
              </form>
            </v-container>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
    <!-- {{ user }}<br/>{{ displayName }} -->
<!-- <input type="file" accept="image/*" capture="camera" /> -->
  </v-container>
</template>

<script>
import * as firebase from 'firebase'

export default {
  data () {
    return {
      error: '',
      phoneNumber: '',
      displayName: '',
      email: '',
      image: null,
      imageUrl: '',
      snapshot2: '',
      imageUrlPreview: '',
      showImageInput: false
    }
  },
  computed: {
    user: function () {
      const user = this.$store.getters.user
      return user
    },
    userId: function () {
      if (this.user) return this.user.id
    },
    imageButtonText: function () {
      return 'Select a NEW Profile Name'
    }
  },
  methods: {
    onSubmit: function () {
      const newProfile = {
        displayName: this.displayName,
        phoneNumber: this.phoneNumber,
        image: this.image,
        email: this.user.email,
        id: this.userId
      }
      this.$store.dispatch('updateUserImageAndProfile', newProfile)
      this.$router.push('/profile')
    },
    onPickFile () {
      this.$refs.fileInput.click()
    },
    onSubmitTest: function () {
      let imageName = this.image.name
      let storageRef = firebase.storage().ref('/userImages/' + imageName)
      storageRef.put(this.image)
        .then((snapshot) => {
          snapshot.ref.getDownloadURL()
            .then((url) => {
              this.imageUrlForDownload = url
              console.log(this.imageUrlForDownload)
            })
        })
        .catch((error) => {
          console.log(error)
        })
    },
    onFilePicked (event) {
      const files = event.target.files
      let filename = files[0].name
      if (filename.lastIndexOf('.') <= 0) {
        alert('Select a valid file')
      }
      const fileReader = new FileReader()
      fileReader.addEventListener('load', () => {
        this.imageUrl = fileReader.result
      })
      fileReader.readAsDataURL(files[0])
      this.image = files[0]
    }
  },
  created () {
  }
}
</script>


