<template>
  <v-layout row wrap>
    <v-container>
      <!-- Search Input -->
      <v-layout row wrap>
        <v-flex xs8>
          <v-text-field
            id='search-schedule'
            name='search-schedule-input'
            label='Search'
            v-model='searchString'
            v-if='showSearchInput'
          >
          </v-text-field>
        </v-flex>          
      </v-layout>
    </v-container>

    <v-flex xs12 v-for='partner in filteredPartners' :key='partner.id'>
      <v-container>
      <v-card>
        <v-card-media :src=logoUrl(partner.logo) height="200px" contain @click="visitUrl(partner.website)" style="cursor:pointer">
        </v-card-media>
        <v-card-title primary-title>
          <div>
            <h3 class="headline mb-0" v-html="partner.organization"></h3>
          </div>
        </v-card-title>
        <v-card-text v-html='partner.description'>
        </v-card-text>
        <v-card-actions @click="visitUrl(partner.website)" style="cursor:pointer">
          <v-btn flat color="orange" :href=fixUrl(partner.website) target="_new">Visit Our Web Site</v-btn>
        </v-card-actions>
      </v-card>
      </v-container>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data () {
    return {
      message: 'Partners',
      searchString: '',
      showSearchInput: true,
      subtype: ''
    }
  },
  computed: {
    filteredPartners: function () {
      let params = {
        searchString: this.searchString,
        subType: this.subType,
        resource: 'partners',
        field: 'organization'
      }
      return this.$store.getters.filteredResource(params)
    }
  },
  methods: {
    logoUrl: function (image) {
      return 'https://charisfellowship.us/images/conference/exhibitors/' + image
    },
    fixUrl: function (url) {
      if (!url.includes('http')) { url = 'http://' + url }
      return url
    },
    visitUrl: function (url) {
      let newUrl = this.fixUrl(url)
      window.open(newUrl)
    }
  },
  created () {
    this.$store.dispatch('updateFromLocalStorage', 'partners')
    this.$store.dispatch('updateFromAPI', 'partners')
  },
  updated () {
    if (this.$route.params.searchString) {
      this.searchString = this.$route.params.searchString
      this.showSearchInput = false
      this.showTabs = false
    }
  }
}
</script>




