<template>
    <div>
    <!-- Container for content with margin -->
    <v-container
      fluid
      style='min-height: 0'
      grid-list-lg
    >
      <!-- Search Input -->
      <v-layout row wrap v-show="showSearchInput">
        <v-flex xs8>
          <v-text-field
            id='search-notifications'
            name='search-notifications-input'
            label='Search'
            v-model='searchString'
            v-show='showSearchInput'
          >
          </v-text-field>
        </v-flex>          
      </v-layout>

      <!-- Show Filters Chatter  -->
      <v-layout row wrap v-show="searchString.length">
        <v-flex xs8>
          <h3 @click='toggleSearchString' style="cursor:pointer"> Showing notifications by {{ searchString }} [clear]</h3>
        </v-flex>          
      </v-layout>

      <!-- Post new chat -->
      <v-layout row wrap>
        <v-flex xs12 v-show="!searchString.length">
            <form>
            <v-text-field
              v-model="message"
              label="Message"
              multi-line
            ></v-text-field>
            <v-btn @click="submitNotification">submit</v-btn>
          </form>
        </v-flex> 

        <!-- List of Chats -->
        <v-flex xs12 v-for='(notification, index) in filteredNotifications' :key='index'>
          <v-card color='grey darken-4' class='white--text'>
          <v-card-text>
              <div v-html='formatChat(notification.message)'></div>
          </v-card-text>
          </v-card>
        </v-flex>
      </v-layout>

    </v-container>
    </div>
</template>

<script>
import * as firebase from 'firebase'
// import store from '../store/store'

export default {
  data () {
    return {
      message: '',
      notifications: [],
      name: '',
      email: '',
      chat: '',
      valid: true,
      searchString: '',
      showSearchInput: false,
      limitToFirst: 50
    }
  },
  computed: {
    filteredNotifications: function () {
      return this.notifications
    },
    filteredNotificationsCount: function () {
      return this.filteredNotifications.length
    },
    notificationsCount: function () {
      return this.notifications.length
    },
    userId: function () {
      return this.$store.getters.userId
    }
  },
  methods: {
    submitNotification: function () {
      alert('worked')
      let obj = {}
      obj.message = this.message
      obj.name = this.$store.getters.user.displayName
      obj.userid = this.$store.getters.user.id
      obj.datetime = Date.now()
      obj.reverseDatetime = -Date.now()
      firebase.database().ref('notifications').push(obj).then(console.log('pushed')).catch((error) => console.log(error))
      this.message = ''
      this.name = ''
    },
    snapshotToArray: function (snapshot) {
      var returnArr = []
      snapshot.forEach(function (childSnapshot) {
        var item = childSnapshot.val()
        item.key = childSnapshot.key
        returnArr.push(item)
      })
      return returnArr
    },
    formatChat: function (chat) {
      return chat.replace(/\n/g, '<br/>')
    },
    isPostMine: function (chatUserId, thisUserId = this.userId) {
      return chatUserId !== thisUserId || this.$store.getters.user.id === 'wjwPJF5wBiYuA1W5JVVLhtv0JFz1'
    },
    deletePost: function (key) {
      firebase.database().ref('notifications').child(key).remove().then(console.log('removed')).catch((error) => console.log(error))
    }
  },
  created () {
    firebase.database().ref('notifications').orderByChild('reverseDatetime').limitToFirst(this.limitToFirst).on('value', (snapshot) => {
      this.notifications = this.snapshotToArray(snapshot)
    })
  }
}
</script>



