<template>
 <div id="app">
  <v-app id="inspire">
    <div
      id="e3"
      style="max-width: 400px; margin: auto;"
      class="grey lighten-3"
    >
      <v-card>
        <v-container
          fluid
          style="min-height: 0;"
          grid-list-lg
        >
          <v-layout row wrap>
            <v-flex xs12 style="cursor:pointer" @click="showFirstFloor">
              <v-card color="cyan darken-2" class="white--text">
                <v-container fluid grid-list-lg>
                  <v-layout row>
                    <v-flex xs7>
                      <div>
                        <div class="headline">First Floor</div>
                        <div>Celebrations, Meals, Exhibits, and most Cohorts</div>
                      </div>
                    </v-flex>
                    <v-flex xs5>
                      <v-card-media
                        src="https://charisfellowship.us/images/conference/Building_Map_1st_Floor-icon.png"
                        height="125px"
                        contain
                      ></v-card-media>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card>
            </v-flex>
            <v-flex xs12 style="cursor:pointer" @click="showSecondFloor">
              <v-card color="purple" class="white--text">
                <v-container fluid grid-list-lg>
                  <v-layout row>
                    <v-flex xs7>
                      <div>
                        <div class="headline">Second Floor</div>
                        <div>Cohorts</div>
                      </div>
                    </v-flex>
                    <v-flex xs5>
                      <v-card-media
                        src="https://charisfellowship.us/images/conference/Building_Map_2nd_floor_icon.jpg"
                        height="125px"
                        contain
                      ></v-card-media>
                    </v-flex>
                  </v-layout>
                </v-container>
              </v-card>
            </v-flex>
          </v-layout>
        </v-container>
      </v-card>
    </div>
  </v-app>
</div>
</template>

<script>
export default {
  data () {
    return {
      message: 'Maps',
      maps: [
        {
          title: 'First Floor',
          imageSrc: 'https://charisfellowship.us/images/conference/Building_Map_1st_Floor-icon.png',
          link: 'https://charisfellowship.us/images/conference/Fremont_Building_Map_1st_floor.jpg'
        },
        {
          title: 'Second Floor',
          imageSrc: 'https://charisfellowship.us/images/conference/Building_Map_2nd_floor_icon.jpg',
          link: 'https://charisfellowship.us/images/conference/Fremont_Building_Map_2nd_floor.jpg'
        }
      ]
    }
  },
  methods: {
    showFirstFloor: function () {
      window.open('https://charisfellowship.us/images/conference/Fremont_Building_Map_1st_floor.jpg')
    },
    showSecondFloor: function () {
      window.open('https://charisfellowship.us/images/conference/Fremont_Building_Map_2nd_floor.jpg')
    }
  }
}
</script>



