<template>
<v-container>
<v-form ref="form" v-model="valid" lazy-validation>
    <v-text-field
      v-model="name"
      :rules="nameRules"
      label="Name"
      required
    ></v-text-field>
    <v-text-field
      v-model="email"
      :rules="emailRules"
      label="E-mail"
      required
    ></v-text-field>
    <v-card>
        <v-subheader><h3>Theme{{tickInWords[theme]}}</h3></v-subheader>
        <v-card-text>  
          <v-slider
              v-model=theme
              :tick-labels="ticksLabels"
              :max="4"
              step="1"
              ticks="always"
              tick-size="2"
              inverse-label
            ></v-slider>
        </v-card-text>
        <v-divider></v-divider>
        <v-subheader><h3>Venue{{tickInWords[venue]}}</h3></v-subheader>
        <v-card-text>  
          <v-slider
              v-model=venue
              :tick-labels="ticksLabels"
              :max="4"
              step="1"
              ticks="always"
              tick-size="2"
            ></v-slider>
        </v-card-text>
        <v-divider></v-divider>
        <v-subheader><h3>Worship Music{{tickInWords[music]}}</h3></v-subheader>
        <v-card-text>  
          <v-slider
              v-model=music
              :tick-labels="ticksLabels"
              :max="4"
              step="1"
              ticks="always"
              tick-size="2"
            ></v-slider>
        </v-card-text>
        <v-divider></v-divider>
        <v-subheader><h3>Speakers{{tickInWords[speakers]}}</h3></v-subheader>
        <v-card-text>  
          <v-slider
              v-model=speakers
              :tick-labels="ticksLabels"
              :max="4"
              step="1"
              ticks="always"
              tick-size="2"
            ></v-slider>
        </v-card-text>
        <v-divider></v-divider>
       <v-subheader><h3>Cohorts{{tickInWords[cohorts]}}</h3></v-subheader>
        <v-card-text>  
          <v-slider
              v-model=cohorts
              :tick-labels="ticksLabels"
              :max="4"
              step="1"
              ticks="always"
              tick-size="2"
            ></v-slider>
        </v-card-text>
        <v-divider></v-divider>
       <v-subheader><h3>I wish we would offer these cohorts next year...</h3></v-subheader>
       <v-card-text>
           <v-textarea
            v-model=nextYear
            multi-line
          ></v-textarea>
        </v-card-text>
        <v-divider></v-divider>
       <v-subheader><h3>How will you leverage access relationships in your ministry this next year?</h3></v-subheader>
          <v-card-text>
           <v-textarea
            v-model=leverage
            multi-line
          ></v-textarea>
        </v-card-text>  
        <v-divider></v-divider>
       <v-subheader><h3>Any other suggestions for Access2019?</h3></v-subheader>
          <v-card-text>
           <v-textarea
            v-model=suggestions
            multi-line
          ></v-textarea>
          </v-card-text>
        <v-divider></v-divider>
       <v-subheader><h3>Are you planning to attend Access2019?</h3></v-subheader>
          <v-card-text>
          <v-radio-group v-model=access2019 row>
            <v-radio label="Yes" value="yes" ></v-radio>
            <v-radio label="No" value="no"></v-radio>
            <v-radio label="Unsure" value="unsure" ></v-radio>
          </v-radio-group>
        </v-card-text>  
    </v-card>  
    <v-btn
      :disabled="!valid"
      @click="submit"
    >
      submit
    </v-btn>
  </v-form>
  </v-container>
</template>

<script>
  import * as firebase from 'firebase'

  export default {
    data: () => ({
      valid: true,
      name: '',
      nameRules: [
        v => !!v || 'Name is required'
      ],
      email: '',
      emailRules: [
        v => !!v || 'E-mail is required',
        v => /.+@.+/.test(v) || 'E-mail must be valid'
      ],
      select: null,
      items: [
        'Item 1',
        'Item 2',
        'Item 3',
        'Item 4'
      ],
      checkbox: false,
      ticksLabels: [
        'Awesome',
        'Good',
        'OK',
        'Didn\'t work for me',
        'No Opinion'
      ],
      theme: '0',
      music: '0',
      venue: '0',
      cohorts: '0',
      speakers: '0',
      nextYear: '',
      leverage: '',
      suggestions: '',
      access2019: '',
      tickInWords: {
        0: ': Awesome!',
        1: ': pretty good.',
        2: ': just OK',
        3: ': didn\'t work for me.',
        4: ': I have no opinion about this.'
      },
      questions: [
        {
          'fieldName': 'theme',
          'question': 'Theme',
          'type': 'slider'
        },
        {
          'fieldName': 'venue',
          'question': 'Venue',
          'type': 'slider'
        },
        {
          'fieldName': 'Worship Music',
          'question': 'Music',
          'type': 'slider'
        },
        {
          'fieldName': 'speakers',
          'question': 'Main Session Speakers',
          'type': 'slider'
        },
        {
          'fieldName': 'cohorts',
          'question': 'Cohorts',
          'type': 'slider'
        },
        {
          'fieldName': 'nextYear',
          'question': 'I wish we would offer these cohorts next year...',
          'type': 'text'
        },
        {
          'fieldName': 'leverage',
          'question': 'How will you leverage access relationships in your ministry this next year?',
          'type': 'text'
        },
        {
          'fieldName': 'suggestions',
          'question': 'Any other suggestions for Access2019?',
          'type': 'yesNo'
        }
      ]
    }),
    computed: {
      inWords: function (tick) {

      }
    },
    methods: {
      submit () {
        let self = this
        let obj = {
          'name': this.name,
          'email': this.email,
          'theme': this.theme,
          'venue': this.venue,
          'music': this.music,
          'speakers': this.speakers,
          'cohorts': this.cohorts,
          'leverage': this.leverage,
          'suggestions': this.suggestions,
          'datetime': Date.now(),
          'reverseDatetime': -Date.now()
        }
        firebase.database().ref('surveys').push(obj)
          .then(function () {
            console.log('pushed')
            self.goToThankyou()
          })
          .catch((error) => console.log(error))
      },
      clear () {
        this.$refs.form.reset()
      },
      goToThankyou () {
        this.$router.push('/surveyComplete')
      }
    }
  }
</script>



