<template>
  <div id='app'>
    <v-form>
      <v-container>
        <v-layout row>
          <v-flex>
            <v-text-field label="Subject" v-model='subject'>
            </v-text-field> 
          </v-flex>
        </v-layout> 
        <v-layout> 
          <v-flex>
            <wysiwyg v-model="content" />
          </v-flex>
        </v-layout>
        <v-layout row>
          <v-flex>
            <v-text-field label="Author" v-model='author'>
            </v-text-field> 
          </v-flex>
        </v-layout> 
        <v-layout row>
          <v-flex>
            <v-date-picker v-model="postDate" :landscape="landscape" :min='minDate' :max="maxDate" required></v-date-picker>
            <v-time-picker v-model="postTime" :landscape="landscape" required></v-time-picker>
          </v-flex>
        </v-layout>
        <v-layout row>
          <v-btn @click="onSubmit">Submit</v-btn>  
        </v-layout>
      </v-container>
    </v-form>
  </div>
</template>

<script>
import axios from 'axios'

export default {
  data () {
    return {
      message: 'Create a new announcement',
      subject: '',
      content: '',
      link: '',
      author: '',
      approved: '',
      landscape: true,
      postTime: '11:00',
      minDate: '2018-07-23',
      maxDate: '2018-08-26'
    }
  },
  computed: {
    postAt () {
      return this.postDate + ' ' + this.postTime
    },
    postDate () {
      return this.minDate
    },
    postApi () {
      if (this.host === 'localhost') {
        return 'http://fgbc:8080/index.cfm?controller=conference.announcements&action=postFromjson'
      } else {
        return 'https://charisfellowship.us/api/announcement/post/'
      }
    },
    host () {
      return window.location.hostname
    }
  },
  methods: {
    onSubmit () {
      let obj = {
        subject: this.subject,
        content: this.content,
        author: this.author,
        postAt: this.postAt,
        approved: 'no'
      }
      console.log(this.postApi)
      console.log(obj)
      axios.post(this.postApi, obj)
        .then(result => {
          console.log(result)
        }).catch(error => {
          console.log(error)
        })
    }
  }
}
</script>


