import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
import Schedule from '@/components/schedule'
import Welcome from '@/components/welcome'
import Meals from '@/components/meals'
import Maps from '@/components/maps'
import Workshops from '@/components/workshops'
import Cohorts from '@/components/cohorts'
import Cohortquestions from '@/components/cohortquestions'
import Chat from '@/components/chat'
import Announcements from '@/components/announcements'
import AnnouncementDialog from '@/components/announcementDialog'
import Resources from '@/components/resources'
import ResourcesDialog from '@/components/resourcesDialog'
import Speakers from '@/components/speakers'
import Staff from '@/components/staff'
import Signup from '@/components/signup'
import Signin from '@/components/signin'
import Profile from '@/components/profile'
import Partners from '@/components/partners'
import Childcare from '@/components/childcare'
import ChildcareSchedule from '@/components/childcareschedule'
import Notifications from '@/components/notifications'
import SurveyDialog from '@/components/surveyDialog'
import SurveyComplete from '@/components/surveyCOmplete'
import ProfileDialog from '@/components/profileDialog'
import Badges from '@/components/badges'
import AuthGuard from './auth-guard'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/home'
    },
    {
      path: '/schedule/:schedule2',
      name: 'Schedule2',
      component: Schedule
    },
    {
      path: '/schedule',
      name: 'Schedule',
      component: Schedule
    },
    {
      path: '/welcome',
      name: 'Welcome',
      component: Welcome
    },
    {
      path: '/meals',
      name: 'Meals',
      component: Meals
    },
    {
      path: '/meals/:searchString',
      name: 'Meal',
      component: Meals
    },
    {
      path: '/maps',
      name: 'Maps',
      component: Maps
    },
    {
      path: '/childcare',
      name: 'Childcare',
      component: Childcare
    },
    {
      path: '/childcareschedule',
      name: 'ChildcareSchedule',
      component: ChildcareSchedule
    },
    {
      path: '/partners',
      name: 'Partners',
      component: Partners
    },
    {
      path: '/partners/:searchString',
      name: 'Partner',
      component: Partners
    },
    {
      path: '/workshops',
      name: 'Workshops',
      component: Workshops
    },
    {
      path: '/badges',
      name: 'Badges',
      component: Badges,
      beforeEnter: AuthGuard
    },
    {
      path: '/chat',
      name: 'Chat',
      component: Chat,
      beforeEnter: AuthGuard
    },
    {
      path: '/home',
      name: 'Home',
      component: Home
    },
    {
      path: '/cohorts',
      name: 'Cohorts',
      component: Cohorts
    },
    {
      path: '/cohortquestions',
      name: 'CohortQuestions',
      component: Cohortquestions
    },
    {
      path: '/cohortquestions/:searchString',
      name: 'CohortQuestion',
      component: Cohortquestions
    },
    {
      path: '/cohorts/:searchString',
      name: 'Cohort',
      component: Cohorts
    },
    {
      path: '/announcements',
      name: 'Announcements',
      component: Announcements
    },
    {
      path: '/announcementDialog',
      name: 'AnnouncementDialog',
      component: AnnouncementDialog
    },
    {
      path: '/resources',
      name: 'Resources',
      component: Resources
    },
    {
      path: '/resources/:searchString',
      name: 'Resource',
      component: Resources
    },
    {
      path: '/resourcesDialog',
      name: 'ResourcesDialog',
      component: ResourcesDialog,
      beforeEnter: AuthGuard
    },
    {
      path: '/resourcesDialog/:cohort',
      name: 'ResourceDialog',
      component: ResourcesDialog,
      beforeEnter: AuthGuard
    },
    {
      path: '/resourceDialogEdit/:key',
      name: 'ResourceDialogEdit',
      component: ResourcesDialog
    },
    {
      path: '/speakers',
      name: 'Speakers',
      component: Speakers
    },
    {
      path: '/speakers/:searchString',
      name: 'Speaker',
      component: Speakers
    },
    {
      path: '/staff',
      name: 'Staff',
      component: Staff
    },
    {
      path: '/signup',
      name: 'Signup',
      component: Signup
    },
    {
      path: '/signin',
      name: 'Signin',
      component: Signin
    },
    {
      path: '/profile',
      name: 'Profile',
      component: Profile,
      beforeEnter: AuthGuard
    },
    {
      path: '/profileRe',
      name: 'ProfileRe',
      component: Profile
    },
    {
      path: '/profileDialog',
      name: 'ProfileDialog',
      component: ProfileDialog
    },
    {
      path: '/notifications',
      name: 'Notifications',
      component: Notifications
    },
    {
      path: '/surveyDialog',
      name: 'SurveyDialog',
      component: SurveyDialog
    },
    {
      path: '/surveyComplete',
      name: 'SurveyComplete',
      component: SurveyComplete
    },
    {
      path: '*',
      component: Home
    }
  ],
  mode: 'history'
})



