<<template>
  <v-layout>
    <v-flex xs12 sm6 offset-sm3>
      <v-card>
        <v-card-media :src="imageUrl" height="200px" contain v-if="showImage">
        </v-card-media>
        <v-card-title primary-title>
          <div>
            <h3 class="headline mb-0">{{ displayName }}</h3>
            <div>{{ phoneNumber }}</div>
          </div>
        </v-card-title>
        <v-card-actions>
          <v-btn flat color="orange" @click="onSubmit">Make Changes</v-btn>
        </v-card-actions>
      </v-card>
    </v-flex>
  </v-layout>
</template>

<script>
export default {
  data () {
    return {
      showImage: false
    }
  },
  computed: {
    displayName: function () {
      return this.$store.getters.user.displayName
    },
    phoneNumber: function () {
      return this.$store.getters.user.phoneNumber
    },
    imageUrl: function () {
      return this.$store.getters.user.imageUrl
    }
  },
  methods: {
    onSubmit: function () {
      this.$router.push('/profileDialog')
    }
  },
  created () {
  }
}
</script>




