<template>
  <div
    id='speakers'
    style='max-width: 900px margin: auto'
    class='grey lighten-3'
  >

    <v-container fluid>
        <!-- List of Meals -->
        <v-layout row wrap>
          <v-flex xs12 v-for='speaker in filteredSpeakers' :key='speaker.id'>
            <v-card :color='cardColor' class='white--text'>
              <v-card-title primary-title>
                <div class="headline">
                  {{ speaker.fname }} {{ speaker.lname }}
                </div>
                <v-spacer></v-spacer>                      
                <v-avatar
                size="100px"
                >
                  <img
                    :src="picUrl(speaker.pic120x120)"
                    alt=""
                  >
                </v-avatar>
              </v-card-title>
              <v-card-text>
                {{ speaker.bioweb }}
              </v-card-text>
            </v-card>
            <v-card-text>
            </v-card-text>
          </v-flex>
        </v-layout>
  </v-container>
  </div>

</template>

<script>
export default {
  data () {
    return {
      showSearchInput: true,
      showTabs: true,
      active: '',
      subType: '',
      field: 'title',
      tabs: ['Tuesday', 'Wednesday', 'Thursday']
    }
  },
  computed: {
    filteredSpeakers: function () {
      let self = this
      let params = {
        searchString: self.searchString,
        subType: self.subType,
        resource: 'speakers',
        field: 'lname'
      }
      console.log(params)
      return this.$store.getters.filteredResource(params)
    },
    searchString: function () {
      return this.$route.params.searchString || ''
    },
    cardColor: function () {
      return this.$store.getters.scheduleColors.speakers
    }
  },
  methods: {
    picUrl: function (file) {
      return 'https://charisfellowship.us/images/conference/instructors/' + file
    }
  },
  created () {
    this.$store.dispatch('updateFromLocalStorage', 'speakers')
    this.$store.dispatch('updateFromAPI', 'speakers')
  },
  updated () {
    if (this.$route.params.searchString) {
      this.searchString = this.$route.params.searchString
      this.showSearchInput = false
      this.showTabs = false
    }
  }
}
</script>




