<template>
  <div
    id='schedule'
    style='max-width: 900px margin: auto'
    class='grey lighten-3'
  >

    <!-- Tabs show at the top wall to wall -->
    <v-tabs
      v-model='active'
      color='cyan'
      dark
      slider-color='yellow'
      v-show='showTabs === true'
    >
    <v-tab @click='setDayOn("Search")'>
      Schedule: 
    </v-tab>
      <v-tab
        v-for='tab in tabs'
        :key='tab'
        ripple
        @click='setDayOn(tab)'
      >
        {{ tab }}
      </v-tab>
    </v-tabs>

    <!-- Container for content with margin -->
    <v-container fluid>

      <!-- Search Input -->
      <v-layout row wrap>
        <v-flex xs8>
          <v-text-field
            id='search-schedule'
            name='search-schedule-input'
            label='Search'
            v-model='searchString'
            v-if='showSearchInput'
          >
          </v-text-field>
          <span v-if='showStatus' v-html='status' />
        </v-flex>          
      </v-layout>  

      <v-layout row v-if='showDayOn && !$route.params.schedule2'>
        <v-flex xs12>
          <v-card dark>
            <v-card-title primary-title>
              <h3 class="headline mb-0" style="text-align:center">
                {{dayOn}}
              </h3>
            </v-card-title>
          </v-card>
        </v-flex>
      </v-layout>
<br/>

      <!--List of Events-->
      <v-layout row wrap>
        <v-flex xs12 v-for='event in filteredSchedule' :key='event.id'>
          <v-card :color='categoryColor(event.category)' class='white--text'>
            <v-card-title primary-title>
              <div class="headline">
                {{ event.description }} @ {{ event.starttime }}
              </div>
              <v-btn
                color="green"
                dark
                small
                absolute
                middle
                right
                fab
                @click="goToCategoryPageFiltered(event.category, getSearchTextFromTitle(event.description))"
              >
              <v-icon>arrow_forward</v-icon>
            </v-btn>              
             </v-card-title>
            <v-card-text>
              {{ event.dayon }} from {{ event.starttime }} to {{ event.endtime }} in the {{ event.eventroom }}
            </v-card-text>
            <v-card-text v-html='event.descriptionprogram'>
            </v-card-text>
            <v-card-text v-html='event.eventroom'>
            </v-card-text>
          </v-card>
          <v-card-text>
          </v-card-text>
        </v-flex>
      </v-layout>

    </v-container>

  </div>
</template>

<script>

export default {
  // store,
  data () {
    return {
      searchString: '',
      showSearchInput: true,
      showTabs: true,
      showDayOn: true,
      active: '',
      subType: '',
      field: 'description',
      tabs: ['Tuesday', 'Wednesday', 'Thursday', 'Search All'],
      dayOn: 'Tuesday',
      categoryRoutesAndColors: {
        'Meal': {route: '/meals', color: this.$store.getters.scheduleColors['meals']},
        'Other-Public': {route: '/cohorts', color: this.$store.getters.scheduleColors['cohorts']},
        'Celebration': {route: '/speakers', color: this.$store.getters.scheduleColors['speakers']},
        'Workshop': {route: '/cohorts', color: this.$store.getters.scheduleColors['cohorts']}
      }
    }
  },
  computed: {
    status: function () {
      return this.$store.getters.status
    },
    showStatus: function () {
      return this.$store.getters.showStatus
    },
    schedule: function () {
      return this.$store.getters.schedule
    },
    filteredSchedule: function () {
      let params = {
        searchString: this.searchString,
        subType: this.subType,
        resource: 'schedule',
        field: 'description'
      }
      if (this.dayOn) {
        return this.$store.getters.scheduleByDay(this.dayOn)
      } else {
        return this.$store.getters.filteredResource(params)
      }
    },
    dayOfWeek: function () {
      let weekday = new Array(7)
      weekday[0] = null
      weekday[1] = null
      weekday[2] = 'Tuesday'
      weekday[3] = 'Wednesday'
      weekday[4] = 'Thursday'
      weekday[5] = null
      weekday[6] = null
      let d = new Date()
      d = d.getDay()
      return weekday[d]
    }
  },
  methods: {
    setDayOn: function (day) {
      this.showDayOn = false
      if (!day.includes('Search')) {
        this.dayOn = day
        this.showSearchInput = false
        this.showDayOn = true
      } else {
        this.dayOn = ''
        this.showSearchInput = true
      }
    },
    categoryRoute: function (category) {
      return this.categoryRoutesAndColors[category].route
    },
    goToCategoryPageFiltered: function (category, title) {
      let searchTextFromTitle = this.getSearchTextFromTitle(title)
      if (searchTextFromTitle) {
        this.$router.push(this.categoryRoute(category) + '/' + searchTextFromTitle)
      } else {
        this.$router.push(this.categoryRoute(category))
      }
    },
    getSearchTextFromTitle: function (title) {
      let searchText = title
      let newSearchText = null
      if (typeof title === 'string') {
        const searchTerms = ['Assist', 'Livigni', 'Allan', 'Pastors', 'College', 'Seminary', 'Connect', 'Trainer', 'Encompass', 'Chaplains', 'Cone', 'National', 'Sparling', 'Charis']
        searchTerms.forEach(function (e) {
          if (searchText.includes(e)) { newSearchText = e }
        })
        return newSearchText
      }
      return null
    },
    categoryColor: function (category) {
      try {
        return this.categoryRoutesAndColors[category].color
      } catch (err) {
        return 'black'
      }
    }
  },
  created () {
    if (this.dayOfWeek) {
      this.setDayOn(this.dayOfWeek)
      this.showDayOn = true
    }
    this.$store.dispatch('updateFromLocalStorage', 'schedule')
    this.$store.dispatch('updateFromAPI', 'schedule')
  }
}
</script>


